import {
  Menu,
  MenuButton,
  MenuDivider,
  MenuGroup,
  MenuItem,
  MenuList,
} from "@chakra-ui/react"
import { graphql, useStaticQuery } from "gatsby"
import React from "react"
import { IoMdMenu } from "react-icons/io"

const CategoryMenu = ({ size }) => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(limit: 2000) {
        group(field: { frontmatter: { category: SELECT } }) {
          fieldValue
          totalCount
        }
      }
    }
  `)

  const categories = data.allMarkdownRemark.group
  return (
    <Menu>
      <MenuButton>
        <IoMdMenu size={size} />
      </MenuButton>
      <MenuList>
        <MenuGroup title="🛠️" type="radio" fontSize="lg">
          {categories.map(category => {
            if (!category.fieldValue.includes("n_")) {
              return (
                <MenuItem
                  as="a"
                  key={category.fieldValue}
                  href={`/categories/${category.fieldValue}`}
                  command={category.totalCount}
                >
                  {category.fieldValue}
                </MenuItem>
              )
            }
          })}
        </MenuGroup>
        <MenuDivider />
        <MenuGroup title="💭" type="radio" fontSize="lg">
          {categories.map(category => {
            if (category.fieldValue.includes("n_")) {
              const categorySubstr = category.fieldValue.substr(2)
              return (
                <MenuItem
                  as="a"
                  key={category.fieldValue}
                  href={`/categories/${category.fieldValue}`}
                  command={category.totalCount}
                >
                  {categorySubstr} 
                </MenuItem>
              )
            }
          })}
        </MenuGroup>
      </MenuList>
    </Menu>
  )
}

export default CategoryMenu
